/**
 * Marks an input of type "radio" as part of the NgbRadioGroup.
 */

export class NgbRadio implements OnDestroy {
  private _value: any = null;
  private _checked: boolean;

  /**
   * You can specify model value of a given radio by binding to the value property.
  */
  
  set value(value: any) {
    this._value = value;
    const stringValue = value ? value.toString() : '';
    this._renderer.setElementProperty(this._element.nativeElement, 'value', stringValue);

    if (this._group) {
      this._group.onRadioValueUpdate();
    }
  }

  get value() { return this._value; }

  get isChecked() { return this._checked; }

  constructor(
       private _group: NgbRadioGroup,  private _label: NgbActiveLabel,
      private _renderer: Renderer, private _element: ElementRef) {
    if (this._group) {
      this._group.register(this);
    }
  }

  markChecked(value) {
    this._checked = (this.value === value && value !== null);
    this._label.active = this._checked;
  }

  ngOnDestroy() {
    if (this._group) {
      this._group.unregister(this);
    }
  }

  onChange() {
    if (this._group) {
      this._group.onRadioChange(this);
    }
  }
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{selector: 'input[type=radio]', host: {'(change)': 'onChange($event.target.value)', '[checked]': 'isChecked'}}, ] },
];
/** @nocollapse */
static ctorParameters: ({type: any, decorators?: DecoratorInvocation[]}|null)[] = [
{type: NgbRadioGroup, decorators: [{ type: Optional }, ]},
{type: NgbActiveLabel, decorators: [{ type: Optional }, ]},
{type: Renderer, },
{type: ElementRef, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'value': [{ type: Input, args: ['value', ] },],
};
}

export const NGB_RADIO_DIRECTIVES = [NgbRadio, NgbActiveLabel, NgbRadioGroup];

